@import '_variables.sass';
.header {
    &__body {
        background-color: $kurenai;
        min-height: 100px;
        padding-top: 25px;
        .container {
            height: 100%; } }
    &__items {
        display: flex;
        justify-content: space-around;
        align-items: center;
        color: white;
        font-size: 20px; }
    &__logo {
        position: absolute;
        top: 0;
        left: 250px;
        img {
            width: 80px;
            height: 100px; } }
    &__text {
        font-weight: 700; }
    a {
        text-decoration: none;
        color: white;
        &:hover {
            color: $teal; } }
    &__contact {
        background-color: white;
        color: $kurenai;
        border: solid 2px;
        border-radius: 25px;
        padding: 10px;
        cursor: pointer; }
    &__nav {
        background-color: $kurenai-light;
        min-height: 60px; }
    &__row {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding-top: 17px;
        &-item {
            color: white;
            text-decoration: underline;
            underline: 2px;
            font-weight: 500;
            font-size: 16px;
            cursor: pointer;
            &:hover {
                color: $ai-light; } } } }
.performance {
    min-height: 400px;
    width: 100%;
    background: linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.3)), url(../img/performance-bg.png) center no-repeat;
    background-size: cover;
    padding-top: 40px;
    &__info {
        display: flex;
        justify-content: space-around;
        align-items: center;
        min-height: 500px; }
    &__targets {
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap;
        width: 35%;
        min-height: 300px;
        color: $brown;
        &-item {
            width: 170px;
            height: 135px;
            background-color: $kurenai-light;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            border-radius: 15px;
            div {
                font-size: 40px;
                font-weight: 800; }
            &:hover {
                background-color: $kurenai-dark;
                text-shadow: 1px 1px 2px black; } } }
    .conty {
        display: flex;
        justify-content: center;
        align-items: center; }
    &__slider {
        position: relative;
        width: 60%;
        min-height: 300px;
        z-index: 1;
        button {
            background: none;
            border: none; }
        button:focus {
            outline: none; }
        &-item {
            img {
                width: 100%;
                height: 100%; } } }
    .slick-prev, .slick-next {
        width: 90px;
        height: 70px; }
    .right-slick, .left-slick {
        width: 90%;
        height: 90%; }
    .slick-next {
        border: none;
        display: block;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 88%; }
    .slick-prev {
        display: block;
        position: absolute;
        top: 50%;
        left: -2%;
        transform: translateY(-50%);
        z-index: 2; }
    &__secondhalf {
        height: 500px;
        margin-top: 20px;
        display: flex;
        justify-content: center;
        align-items: center; }
    &__secondslider {
        width: 60%;
        height: 66%;
        button {
            background: none;
            border: none; }
        button:focus {
            outline: none; }
        img {
            display: block;
            width: 100%;
            height: 100%; }
        .slick-prev, .slick-next {
            width: 90px;
            height: 70px; }
        .right-slick, .left-slick {
            width: 90%;
            height: 90%; }
        .slick-next {
            border: none;
            display: block;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 88%; }
        .slick-prev {
            display: block;
            position: absolute;
            top: 50%;
            left: 0%;
            transform: translateY(-50%);
            z-index: 2; } }
    &__descr {
        margin-left: 20px;
        height: 66%;
        width: 40%;
        color: $brown;
        background-color: $kurenai-light;
        border-radius: 30px;
        padding: 5px;
        &-title {
            font-size: 30px;
            text-align: center;
            font-weight: 600px; }
        &-text {
            font-size: 18px;
            text-align: center;
            line-height: 24px; }
        &-btn {
            cursor: pointer;
            background-color: $brown;
            color: $kurenai-light;
            border-radius: 10px;
            width: 200px;
            height: 50px;
            text-align: center;
            font-size: 30px;
            margin-left: 105px;
            margin-top: 10px;
            &:hover {
                background-color: $kurenai-dark;
                color: $brown; } } } }
.sports {
    background: linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.3)), url(../img/sportsbg.png) center no-repeat;
    background-size: cover;
    min-height: 500px;
    padding-top: 20px;
    &__title {
        font-size: 40px;
        color: $ai-light;
        text-align: center; }
    &__subtitle {
        font-size: 35px;
        color: white;
        text-align: center; }
    &__items {
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-top: 20px;
        &-item {
            background-color: $kurenai;
            width: 33%;
            padding-left: 110px;
            border-radius: 30px;
            margin-left: 15px;
            height: 200px;
            &:nth-child(1) {
                margin-left: 0; }
            div {
                margin-top: 15px;
                margin-left: 15px;
                font-size: 30px;
                font-weight: bold; }
            a {
                text-decoration: none;
                color: $ai-light;
                &:hover {
                    color: $teal; } }
            ul {
                margin-top: 10px;
                margin-left: 15px;
                li {
                    color: white;
                    font-size: 20px; } } } } }









